<script>
import axios from "axios";
import mapboxgl from "mapbox-gl";

export default {
  name: "MainMap",

  metaInfo: {
    title: process.env.VUE_APP_TITLE,
  },

  data() {
    return {
      updated: "",

      map: {},

      showStatisticsDialog: true,

      headers: [
        { text: "Mapillary –ø—Ä–æ—Ñ—ñ–ª—å", value: "name" },
        { text: "–û–±'—î–∫—Ç–∏ –ü–ó–§ –£–∫—Ä–∞—ó–Ω–∏", value: "paname" },
        { text: "–ö—ñ–ª—å–∫—ñ—Å—Ç—å —Ñ–æ—Ç–æ", value: "count" },
      ],

      statistics: [],
    };
  },

  methods: {
    getPaName(pa) {
      let pacontent = "";
      for (let key in pa) {
        let paname = key;
        let osmid = pa[key];

        let osmtype = "way";

        if (parseInt(osmid) < 0) {
          osmtype = "relation";
          osmid = parseInt(osmid) * -1;
        }

        let paurl = `<a class='links' target="_blank" href="http://www.openstreetmap.org/${osmtype}/${osmid}">${paname}</a>, `;
        pacontent = pacontent + paurl;
      }

      return pacontent.slice(0, -2);
    },
  },

  mounted() {
    mapboxgl.accessToken = process.env.VUE_APP_MAPBOX_ACCESS_TOKEN;
    const map = new mapboxgl.Map({
      container: "map",
      style: "mapbox://styles/hast/ck9grr20x4pz91io7lsa9szdk",
      center: [31.198, 48.642],
      zoom: 6.0,
      hash: true,
    });

    map.addControl(new mapboxgl.ScaleControl(), "bottom-right");
    map.addControl(new mapboxgl.NavigationControl({}), "top-right");

    map.on("load", () => {
      map.addSource("photo_point", {
        type: "geojson",
        data: "photo_point.geojson",
      });

      map.addLayer({
        id: "photo_point",
        type: "circle",
        source: "photo_point",
        layout: {},
        paint: {
          "circle-radius": {
            base: 1,
            stops: [
              [10, 1],
              [15, 2],
              [17, 3],
            ],
          },
          "circle-color": "#345b37",
          "circle-stroke-width": {
            base: 1,
            stops: [
              [15, 0],
              [16, 1],
            ],
          },
          "circle-stroke-color": "hsl(0, 0%, 98%)",
        },
      });
    });

    this.map = map;

    axios({
      method: "get",
      url: "mapillary_data.json",
    })
      .then(
        (response) => {
          this.statistics = response.data;
        },
        (error) => {
          console.log("error", error);
          throw error;
        }
      )
      .catch((error) => {
        console.log("cerror", error);
        throw error;
      });

    axios({
      method: "get",
      url: "upd.json",
    })
      .then(
        (response) => {
          this.updated = response.data;
        },
        (error) => {
          console.log("error", error);
          throw error;
        }
      )
      .catch((error) => {
        console.log("cerror", error);
        throw error;
      });
  },
};
</script>
